.loginScreen {
    position: relative;
    height: 100%;

    background: url("https://img.helpnetsecurity.com/wp-content/uploads/2020/03/23143409/netflix-collection.jpg")
    center no-repeat;
    background-size: cover;

}
.loginScreen__gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgb(0, 0, 0, 0.4);
    background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.8) 0,
        rgba(0, 0, 0, 0) 60%,
        rgba(0, 0, 0, 0.8) 100%
    );
}

.loginScreen__logo{
    position: fixed;
    left: 20px;
    width: 150px;
    object-fit: contain;
}

.loginScreen__button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    cursor: pointer;
    border: none;
    font-weight: 600;
}

.loginScreen__body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginScreen__body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen__body > h3{
    font-size: 1.3rem;
    font-weight: 400;
}


.loginScreen__body{
    top: 30%;
    left: 0;
    right: 0;
    position: absolute;
    z-index: 1;
    color: #fff;
    padding: 20px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.loginScreen__input > form > input{
    width: 30%;
    padding: 10px;
    outline-width: 0;
    height: 30px;
    border: none;
    max-width: 600px;
}

.loginScreen__getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

.loginScreen__input{
    margin: 20px;
}